body{
  margin:0;
  color:#333;
  font-family:sans-serif;
}

.about-title{
  text-align:center;
  font-family: Alcubierre, sans-serif;
  font-size:3em;
  color: rgb(241, 205, 112)
}

#team{
  display:flex;
  flex-wrap: wrap;
  flex-grow:3;
}

.card {
  width:33%;
  min-width: 250px;
  height: 300px;
  overflow: hidden;
  position: relative;
  margin:0 0.5% 1% 0;
  display:inline-block
}

.card:nth-of-type(3n){
  margin-right:0;
}

.card img {
  width: 100%;
  min-height:100%;
  transition:all 0.9s
}

.card:hover img{
 transform:scale(1.2,1.2)
}

.data {
  position: absolute;
  bottom: 5%;
  background: #fff;
  text-align:center;
  width: 90%;
  height: 80px;
  overflow: hidden;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 10px 5%;
  box-sizing: border-box;
  opacity: 0.8;
  transition: all 0.4s
}

.card:hover .data {
  width: 100%;
  height: 100%;
  bottom: 0;
  opacity: 0.9;
  padding:90px 10%
}

.data h2 {
  margin: 0 0 5px 0;
  transition:color 0.4s
}

.data p {
  opacity: 0;
  text-align:justify;
  transition: all 0.2s
}

.card:hover .data p,.card:hover .data a {
  opacity: 1
}

.data a{
  color:#333;
  text-decoration:none;
  padding:20px;
  opacity:0
}

.data a:hover,.card:hover h2{
  color:#38c
}

@media (max-width:550px){
  .card {
    width:100%;
    margin-right:0
  }
}

@media (max-width:2000px){
  .card {
    width:20%;
    margin-right:0;
    margin-top: 5%;
  }
}

.btn-twtr{
  color:#ddd;
  border:2px solid;
  border-radius:3px;
  text-decoration:none;
  display:inline-block;
  padding:5px 10px;
  font-family:sans-serif;
  font-weight:600;
  margin-top:100px;
  opacity:0.8
}

.fa-github, .fa-briefcase, .fa-linkedin {
    color: rgb(100, 74, 7)
}